<template>
  <div class="UserInfo">
    <!-- 在数据未返回的时候，显示正在加载的gif -->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.svg" alt />
    </div>

    <div class="userInformation">
      <section>
        <img :src="userInfo.avatar_url" alt />
        <span>{{userInfo.loginname}}</span>
        <p>{{userInfo.score}}积分</p>
        <p>注册时间：{{userInfo.create_at | formateDate}}</p>
      </section>

      <div class="replies">
        <p>回复主题</p>
        <ul>
          <li v-for="item in userInfo.recent_replies">
            <router-link :to="{name: 'post_content', params: {id: item.id}}">{{item.title}}</router-link>
          </li>
        </ul>
      </div>

      <div class="topics">
        <p>创建的主题</p>
        <ul>
          <li v-for="item in userInfo.recent_topics">
            <router-link :to="{name: 'post_content', params: {id: item.id}}">{{item.title}}</router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UserInfo",
  data() {
    return {
      isLoading: false,
      userInfo: {}
    };
  },
  methods: {
    getData: function() {
      let self = this;
      this.$http
        .get(` https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(function(res) {
          self.isLoading = false; //加载成功后去除动画
          self.userInfo = res.data.data;
          console.log(res.data.data);
        })
        .catch(function(err) {
          //处理返回失败后的问题
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.isLoading = true; //加载成功之前显示加载动画
    this.getData(); //在页面加载之前获取数据
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.userInformation {
  background: white;
  width: 75%;
  margin: 10px auto;
}

.userInformation section {
  padding: 10px;
}

.userInformation img {
  width: 30px;
}

.userInformation li {
  list-style: none;
}

.userInformation .replies,
.userInformation .topics {
  font-size: 0.72rem;
  border-top: 10px #dddddd solid;
}

.userInformation > div > ul > li {
  padding: 4px 0 4px 12px;
  white-space: nowrap;
  font-size: 0.72rem;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 30px;
  vertical-align: middle;
}

.userInformation > div > ul > li > a {
  color: #094e99;
  text-decoration: none;
}
</style>



