<template>
  <div class="article">
    <!-- 如果正在加载显示此div -->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.svg" alt />
    </div>

    <div class="topic" v-else>
      <div class="topic_header">
        <h2 class="topic_title">{{posts.title}}</h2>
        <ul>
          <li>●发布于：{{posts.create_at | formateDate}}</li>
          <li>●作者：{{posts.author.loginname}}</li>
          <li>●{{posts.visit_count}}次浏览</li>
          <li>●来自{{posts | tabFormatter}}</li>
        </ul>
        <div class="topic_content" v-html="posts.content"></div>
      </div>
    </div>

    <div id="reply">
      <div class="topbar">回复</div>
      <div class="replySec" v-for="(reply,index) in posts.replies">
        <div class="replyUp">
          <router-link :to="{name:'user_info', params: {name: reply.author.loginname}}">
            <img :src="reply.author.avatar_url" alt />
          </router-link>
          <span>{{reply.author.loginname}}</span>
          <span>{{index+1}}楼</span>
          <span v-if="reply.ups.length>0">☝ {{reply.ups.length}}</span>
        </div>
        <p v-html="reply.content"></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      isLoading: false,
      posts: {} //代表当前文章的所有内容，所有属性
    };
  },
  methods: {
    getArticleData: function() {
      var self = this;
      this.$http
        .get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then(function(res) {
          if (res.data.success == true) {
            self.isLoading = false;
            self.posts = res.data.data;
            console.log(self.posts);
          }
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.isLoading = true; //加载成功之前显示加载动画
    this.getArticleData(); //在页面加载之前获取数据
  },
  watch: {
    $route(to, from) {
      this.getArticleData(); //识别同个路由的的地址变化
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@import url("../assets/markdown-github.css");
/* @import url("../assets/ml.css"); */

.loading {
  text-align: center;
  padding-top: 300px;
}

.topbar {
  padding: 10px;
  background-color: #f5f5f5;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

.topic,
#reply {
  margin-right: 340px;
  margin-top: 15px;
}

#reply,
.topic_header {
  background-color: #fff;
}

#reply {
  margin-top: 15px;
}

#reply img {
  width: 30px;
  height: 30px;
  position: relative;
  bottom: -9px;
}

#reply a,
#reply span {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}

.replySec {
  border-bottom: 1px solid #e5e5e5;
  padding: 0 10px;
}

.replyUp a:nth-of-type(2) {
  margin-left: 0px;
  display: inline-block;
}

.topic_header {
  padding: 10px;
}

.topic_title {
  font-size: 20px;
  font-weight: bold;
  padding-top: 8px;
}

.topic_header ul {
  list-style: none;
  margin: 6px 0px;
  display: flex;
  padding-inline-start: 0px;
}

.topic_header li {
  font-size: 12px;
  color: #838383;
  margin: 0 3px;
}

.topic_content {
  border-top: 1px solid #e5e5e5;
  padding: 0 10px;
}

.markdown-text img {
  width: 92% !important;
}
</style>


