<template>
  <div id="app">
    <Header></Header>
    <main>
      <router-view name="slideBar"></router-view>
      <router-view name="main"></router-view> 
    </main> 
  </div>
</template>

<script>
import Header from './components/Header'
import PostList from './components/PostList'

export default {
  name: 'App',
  components: {
    Header,
    PostList
  }
}
</script>

<style>
main {
  width: 80%;
  margin: 0 auto;
}
#app {
  background: rgb(225,225,225);
}
</style>



